<template>
  <div id="lottery">
    <div class="header">
      抽奖 <span style="color: #ff4343;">{{ getLotteries.name }} - {{ getLotteries.prizename }}</span>
    </div>

    <div class="list">
      <transition-group name="slideDown" tag="ul">
        <li v-for="(item, i) in showUsers" :key="i">
          <div class="icon"><img :src="item.headpic" alt=""></div>
          <div class="uname">{{ item.name }}</div>
        </li>
      </transition-group>
    </div>

    <transition name="fade">
      <div class="lottery-ani" v-if="randoming">
        <img :src="randomAva" alt="">
      </div>
    </transition>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  props: ['ws'],
  data() {
    return {
      randomAva: '',
      randoming: false,
      showUsers: [],
    };
  },
  methods: {
    startAni() {
      let i = this.getLotteries.users.length * 50 - 1;
      let showUserNumber = 50;
      let curShowUser = 0;

      i -= 3;
      
      const avaArr = this.getAvas;

      this.randoming = true;
      const timer = setInterval(() => {

        if (i % showUserNumber === 0) {
          this.showUsers.push(this.getLotteries.users[curShowUser]);
          curShowUser ++;
          if (curShowUser >= this.getLotteries.users.length) {
            clearInterval(timer);
            this.randoming = false;

            // console.log(this.ws);
            this.ws.send(JSON.stringify(this.getLotteriesBackData));
            return;
          }
        }

        this.randomAva = avaArr[parseInt(Math.random() * avaArr.length)];
        i --;

      }, 100);

    }
  },
  mounted() {
    this.startAni();

    setTimeout(() => {
      $(".list").animate({
        scrollTop: $('.list ul').height()
      }, 250);
    }, 1000);
  },
  computed: {
    ...mapGetters(['getLotteries', 'getAvas', 'getLotteriesBackData'])
  },
}
</script>

<style lang="scss" scoped>
  #lottery {
    position: fixed;
    width: calc(100% - 40px);
    height: calc(100% - 40px);

    .lottery-ani {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: white;
      width: 200px;
      height: 200px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .header {
      width: 100%;
      height: 60px;
      line-height: 60px;
      padding: 0 10px;
      font-size: 4rem;
      box-sizing: border-box;
      background-color: rgba(0, 0, 0, .4);
      color: white;
    }

    .list {
      height: calc(100% - 80px);
      margin-top: 20px;
      color: white;
      background-color: rgba(0, 0, 0, .4);
      overflow-y: scroll;

      ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;

        li {
          width: 20%;
          height: 190px;
          margin-top: 10px;

          .icon {
            max-width: 150px;
            max-height: 150px;
            margin: 0 auto;

            img {
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }

          .uname {
            text-align: center;
            margin-top: 10px;
            font-size: 2rem;
          }
        }
        
      }  
    }
  }
</style>



