<template>
  <div id="sign">
    <div class="header">
      <span>已签到: {{ getSigns.length }}人</span>
    </div>
    <div class="signer" id="signers">
      <transition-group name="slideDown" tag="ul">
        <li v-for="(item, i) in getSigns" :key="i">
          <div class="icon"><img :src="item.headpic" alt=""></div>
          <div class="uname">{{ item.name }}</div>
        </li>
      </transition-group>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      signed: 1
    };
  },
  mounted() {
    // setInterval(() => {
    //   this.user.push({
    //     headpic: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=16550438,2220103346&fm=27&gp=0.jpg',
    //     name: '号人'
    //   });
    //   $("#signers").animate({
    //     scrollTop: $('#signers ul').height()
    //   }, 250);
    //   this.signed ++;
    // }, 400);

    
  },
  watch: {
    getSigns() {
      setTimeout(() => {
        $("#signers").animate({
          scrollTop: $('#signers ul').height()
        }, 250);
      }, 1000);
    }
  },
  computed: {
    ...mapGetters(["getSigns"])
  },
}
</script>

<style lang="scss" scoped>
  #sign {
    position: fixed;
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    // background-color: rgba(255, 255, 255, .6);
    // display: flex;
    // flex-wrap: wrap;
    // align-items: flex-start;

    .header {
      width: 100%;
      height: 60px;
      line-height: 60px;
      padding: 0 10px;
      font-size: 4rem;
      box-sizing: border-box;
      background-color: rgba(0, 0, 0, .4);
      color: white;
    }

    .signer {
      height: calc(100% - 80px);
      margin-top: 20px;
      color: white;
      background-color: rgba(0, 0, 0, .4);
      overflow-y: scroll;

      ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;

        li {
          width: 20%;
          height: 190px;
          margin-top: 10px;

          .icon {
            max-width: 150px;
            max-height: 150px;
            margin: 0 auto;

            img {
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }

          .uname {
            text-align: center;
            margin-top: 10px;
            font-size: 2rem;
          }
        }
        
      }  
    }
  }
</style>



