<template>
  <div id="cover" :style="{ backgroundImage: `url('${getCover}')` }">
    <transition name="fade">
      <img :src="getCover" alt="" v-if="showCoverImg">  
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      showCoverImg: true
    };
  },
  computed: {
    ...mapGetters(["getCover"])
  }
}
</script>

<style lang="scss" scoped>
  #cover {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-position: top center;
    background-size: cover;

    img {
      width: 100%;
      height: 100%;
    }
  }
</style>



