const mutations = {
  setCover(state, cover) {
    state.cover = cover;
  },
  setLotteries(state, lotteries) {
    state.lotteries = lotteries;
  },
  setImgBarrage(state, imgBarrage) {
    state.imgBarrage = imgBarrage;
  },
  setVote(state, vote) {
    state.vote = vote;
  },
  addBarrage(state, barrage) {
    state.barrages.push(barrage);
  },
  addSign(state, sign) {
    state.signs.push(sign);
  },
  setAvas(state, avas) {
    state.avas = avas;
  },
  setLotteriesBackData(state, ld) {
    state.lotteriesBackData = ld;
  }
};

export default mutations;



