<template>
  <section id="login">
    <div id="form">
      <input type="text" :placeholder="ph" ref="input" v-model="secretkey" @keyup.enter="login">
      <button type="button" @click="login">进入</button>
    </div>
  </section>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      secretkey: '',
      ph: '请输入活动key'
    };
  },
  methods: {
    async login() {

      if (this.secretkey === '') return;
// /screen/login?secretkey=mAwU6
      $.get('/screen/login', {
        secretkey: this.secretkey
      }, (res) => {
        if (res.success) {
          sessionStorage.setItem('in', '1');
          this.$emit('conn');
          return;
        }

        this.secretkey = '';
        this.ph = res.msg;
      });
    }
  },
  mounted() {
    this.$refs.input.focus();
  }
}

// screen/login?secretkey=mAwU6
</script>

<style lang="scss" scoped>
  #login {
    position: fixed;
    z-index: 1;
    width: 50%;
    height: 40px;
    

    #form {
      font-size: 2rem;

      input {
        float: left;
        width: 70%;
        height: 40px;
        border: 0 none;
        box-sizing: border-box;
        padding: 5px;
      }

      button {
        float: left;
        width: 30%;
        height: 40px;
        border: 0 none;
        box-sizing: border-box;
        background: #259beb;
        color: #fff;
        cursor: pointer;

        &:active {
          background: #1b84ca;
        }
      }
    }
  }
</style>



