<template>
  <div>
    <div id="bg" :style="bgStyle"></div>
    <div id="app">
      <transition name="bounceRight">
        <login v-if="!show" @conn="conn" />
      </transition>
      <transition name="bounceLeft">
        <router-view v-if="show"  :ws="ws" />
      </transition>
      <transition name="bounceRight">
        <img v-if="shareImgFlag" :src="shareImg" id="shareImg" style="z-index: 6;" />
      </transition>
      <transition name="bounceDown">
        <img v-if="userShareImgFlag" :src="userShareImg" id="shareImg" :style="shareImgStyle" />
      </transition>
    </div>
  </div>
</template>

<script>
import Login from "./views/Login";
import { mapMutations } from "vuex";

export default {
  name: "App",
  components: {
    Login
  },
  data() {
    return {
      show: false,
      bgStyle: {
        backgroundImage:
          'url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1523003055233&di=1b31a13de28456f71da2388476513c46&imgtype=0&src=http%3A%2F%2Fimg.tuku.cn%2Ffile_big%2F201502%2Ff4490c6cdb9b42cd9d6d856e502fa328.jpg")'
      },
      ws: null,
      // wsURL: "ws://10.28.109.125:8088",
      wsURL: 'ws://192.168.1.110/aws',
      // wsURL: "ws://121.40.165.18:8088",
      shareImg: "",
      // "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1523112395141&di=b3c3f7d2eaf16e8dd8a5fc91e64b65ef&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimage%2Fc0%253Dshijue1%252C0%252C0%252C294%252C40%2Fsign%3Da2df8ac1bcfd5266b3263457c371fd5e%2Fd1160924ab18972bf71386e0eccd7b899e510aea.jpg",
      shareImgFlag: false,

      userShareImg: "",
      // "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1523112395141&di=b3c3f7d2eaf16e8dd8a5fc91e64b65ef&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimage%2Fc0%253Dshijue1%252C0%252C0%252C294%252C40%2Fsign%3Da2df8ac1bcfd5266b3263457c371fd5e%2Fd1160924ab18972bf71386e0eccd7b899e510aea.jpg",
      userShareImgFlag: false,

      signCache: [],
      barrageCache: [],

      getSignedUserTimestamp: 0,
      lotteriesCache: {},
      shareImgStyle: {
        zIndex: 3
      }
    };
  },
  methods: {
    ...mapMutations({
      setCover: "setCover",
      setLotteries: "setLotteries",
      setImgBarrage: "setImgBarrage",
      setVote: "setVote",
      addBarrage: "addBarrage",
      addSign: 'addSign',
      setAvas: 'setAvas',
      setLotteriesBackData: 'setLotteriesBackData'
    }),

    conn() {
      this.show = !this.show;
      this.$router.replace("/barrage");

      this.ws = new WebSocket(this.wsURL);

      console.log(this.ws);

      const ws = this.ws;

      ws.onopen = this.wsonopen;
      ws.onmessage = this.wsonmessage;
    },

    wsonopen(e) {
      console.log('connected.');
    },

    wsonmessage(e) {
      // console.log(e.data);
      const resData = JSON.parse(e.data);
      // const resData = {};

      // const type = resData.type;
      const type = resData.type;
      // console.log(resData);

      switch (type) {
        case 2:
          // 展示海报
          this.cover(resData);
          break;

        case 11:
          // 抽奖
          this.lottery(resData);
          break;

        case 1:
          // 房主分享图片
          this.barrageImg(resData);
          break;

        case 24:
          // 房主取消分享图片
          this.hideBarrageImg(resData);
          break;

        case 14:
          // 投票
          this.votes(resData);
          break;

        case 0:
          // 弹幕
          this.barrage(resData);
          break;

        case 3:
          // 签到
          this.sign(resData);
          break;

        case 23:
          // 用户分享图片
          this.userShareImgHandle(resData);
          break;

        case 22:
          // 获取所有头像
          this.setAllUserHeadPic(resData);

        default:
          break;
      }
    },

    setAllUserHeadPic(data) {
      let arr = [];

      data.data.forEach(item => {
        arr.push('/headpics/' + item.headpic);
      });

      this.setAvas(arr);
      this.setLotteries(this.lotteriesCache.data);
      this.setLotteriesBackData(this.lotteriesCache);
      if (this.$route.fullPath === '/lottery') {
        this.$router.push('/sign');

        setTimeout(() => {
          this.$router.push('/lottery');
        }, 500);
      }
      this.$router.push("lottery");
    },

    // 获取签到的人
    async getSignedUser() {
      const stamp = this.getSignedUserTimestamp;

      // const { data: { success, msg, data } } = await axios.get(`/users/signn?aid=${this.aid}&s=${stamp}`);
      // let { data: { success, msg, data } } = await axios.get(`https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getSignedUser`);

      $.get(
        "/users/sign",
        {
          // $.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getSignedUser', {
          aid: this.aid,
          s: stamp
        },
        res => {
          if (res.success) {
            const data = res.data || [];

            data.forEach(item => {
              item.headpic = '/headpics/' + item.headpic;
            });

            this.signCache = [...this.signCache, ...data];
            this.getSignedUserTimestamp = res.msg;
          }
        }
      );
    },

    // 用户分享图片
    userShareImgHandle(data) {
      // const barrage = {
      //   name: '123',
      //   content: '分享了一张图片',
      //   headpic: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3512331237,2033775251&fm=27&gp=0.jpg'
      // }

      const barrage = {
        name: data.sender.name,
        content: "分享了一张图片",
        headpic: '/headpics/' + data.sender.headpic
      };

      this.addBarrage(barrage);
      // this.userShareImg = 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3512331237,2033775251&fm=27&gp=0.jpg';
      this.userShareImg = '/display-pictures/' + data.data;

      this.$router.push("/barrage");

      const img = new Image();
      img.src = '/display-pictures/' + data.data;

      console.log(img);
      img.onload = () => {
        if (img.width > img.height) {
          this.shareImgStyle = {
            zIndex: 3,
            width: "90%"
          }
        }
        this.shareImgStyle = {
          zIndex: 3,
          height: '90%'
        }

        console.log(img);
        this.userShareImgFlag = true;      

        setTimeout(() => {
          this.userShareImgFlag = false;
        }, 5000);
      };

      
    },

    // 签到
    sign(data) {
      // const sign = {
      //   name: '123',
      //   headpic: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3512331237,2033775251&fm=27&gp=0.jpg'
      // }

      this.aid = data.receiver;
      // this.aid = 6;

      this.getSignedUser();
      // this.addSign(sign);
      this.$router.push("/sign");

      if (!this.popUsers) {
        this.popUsers = setInterval(() => {
          if (this.signCache.length !== 0) {
            this.addSign(this.signCache.pop());
          }
        }, 500);

        setInterval(() => {
          this.getSignedUser();
        }, 10000);
      }
    },

    // 弹幕
    barrage(data) {
      // this.barrageCache.push({
      //   name: '123',
      //   content: '456fdsafdas',
      //   headpic: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3512331237,2033775251&fm=27&gp=0.jpg'
      // });

      if (/\{\/.+?\/\}/.test(data.data)) {
        const matches = data.data.match(/\{\/.+?\/\}/g);

        matches.forEach((item, index) => {1
          const pos = matches[index].match(/-?\d+px/g);
          let str = `<span class="face" style="background-position: ${pos[0]} ${pos[1]};"}></span>`;

          data.data = data.data.replace(item, str);
        });
      }

      console.log(data.data);

      this.barrageCache.push({
        name: data.sender.name,
        content: data.data,
        headpic: '/headpics/' + data.sender.headpic
      });

      console.log({
        name: data.sender.name,
        content: data.data,
        headpic: '/headpics/' + data.sender.headpic
      });

      if (this.$route.fullPath !== "/barrage") this.$router.push("/barrage");

      if (!this.barrageTimer) {
        this.barrageTimer = setInterval(() => {
          if (this.barrageCache.length !== 0) {
            this.addBarrage(this.barrageCache.shift());
          }
        }, 1000);
      }
    },

    // 投票
    votes(data) {
      this.setVote(data.data);
      if (this.$route.fullPath !== "/vote") this.$router.push("/vote");
    },

    // 取消图片分享
    hideBarrageImg(data) {
      this.shareImgFlag = false;
    },

    // 分享图片
    barrageImg(data) {
      if (this.shareImgFlag) return;

      // data.data =
      //   "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1523112395141&di=b3c3f7d2eaf16e8dd8a5fc91e64b65ef&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimage%2Fc0%253Dshijue1%252C0%252C0%252C294%252C40%2Fsign%3Da2df8ac1bcfd5266b3263457c371fd5e%2Fd1160924ab18972bf71386e0eccd7b899e510aea.jpg";
      this.shareImg = '/display-pictures/' + data.data;
      this.shareImgFlag = true;
    },
    // 抽奖
    lottery(data) {
      // data.data = {
      //   name: "二等奖",
      //   prizename: "手机",
      //   number: 1,
      //   users: [
      //     {
      //       id: 88888928,
      //       name: "南宫封清",
      //       sex: "男",
      //       area: "山西 太原",
      //       phone: null,
      //       headpic:
      //         "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2809576112,626361756&fm=27&gp=0.jpg"
      //     },
      //     {
      //       id: 88888928,
      //       name: "南宫封清2",
      //       sex: "男",
      //       area: "山西 太原",
      //       phone: null,
      //       headpic:
      //         "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2809576112,626361756&fm=27&gp=0.jpg"
      //     }
      //   ]
      // };
      data.data.users.forEach(item => {
        item.headpic = '/headpics/' + item.headpic;
      });
      
      this.lotteriesCache = data;

      this.ws.send(JSON.stringify({
        type: 22,
        receiver: data.receiver
      }));
    },
    // 展示海报
    cover(data) {
      this.setCover('/advertisings/' + data.data);
      // this.setCover('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1523112395141&di=b3c3f7d2eaf16e8dd8a5fc91e64b65ef&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimage%2Fc0%253Dshijue1%252C0%252C0%252C294%252C40%2Fsign%3Da2df8ac1bcfd5266b3263457c371fd5e%2Fd1160924ab18972bf71386e0eccd7b899e510aea.jpg');
      this.$router.push("/cover");
    }
  },
  mounted() {
    const loged = sessionStorage.getItem("in") || "0";

    if (loged === '1') {
      this.conn();
    }
  }
};
</script>

<style>
.face {
  display: inline-block;
  width: 30px;
  height: 30px;
  background: url('./assets/images/e.png') no-repeat;
  background-size: 200px auto;
  cursor: pointer;
  border: none;
}

#shareImg {
  position: fixed;
  width: 70%;
  border: 4px solid white;
}

#app {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-position: top center;
  background-size: cover;
  filter: brightness(0.7);
}
</style>



