<template>
  <div id="vote">
    <div class="vote-title">
      投票: {{ getVote.name }}
    </div>

    <div id="vote-list">
      <!-- <transition-group name="slideDown" tag="ul">
        <li v-for="(item, i) in voteInfo.options" :key="i">
          <div class="icon"><img :src="item.headpic" alt=""></div>
          <div class="uname">{{ item.name }}</div>
        </li>
      </transition-group> -->
      <ul>
        <li v-for="(item, i) in getVote.voteGroups" :key="i">
          <div class="option">
            <div class="title">
              {{ item.name }}
            </div>
            <div class="number">
              {{ item.number }} 票
              
            </div>
          </div>
          <div class="rate">
            <div class="progress">
              <div class="progress-bar progress-bar-striped active" :class="randBarColor()" role="progressbar" aria-valuemin="0" aria-valuemax="100" :style="{width: `${item.number / getVote.voteSum * 100}%`}">
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  methods: {
    randBarColor() {
      return ['progress-bar-success', 'progress-bar-info', 'progress-bar-warning', 'progress-bar-danger'][parseInt(Math.random() * 4)];
    }
  },
  data() {
    return {
    }
  },
  computed: {
    ...mapGetters(["getVote"])
  },
  mounted() {
    // console.log(this.getVote);
  }
}
</script>

<style lang="scss" scoped>
  #vote {
    position: fixed;
    width: calc(100% - 40px);
    height: calc(100% - 40px);

    .vote-title {
      width: 100%;
      height: 60px;
      box-sizing: border-box;
      line-height: 60px;
      padding: 0 10px;
      font-size: 4rem;
      color: white; 
      background-color: rgba(0, 0, 0, .4);
    }

    #vote-list {
      background-color: rgba(0, 0, 0, .4);
      height: calc(100% - 80px);
      margin-top: 20px;

      ul {
        list-style-type: none;
        height: 100%;
        padding: 0;
        margin: 0;
        display: flex;
        flex-direction: column;

        li {
          flex: 1;
          color: white;
          display: flex;
          align-items: center;

          .option {
            width: 200px;
            text-align: center;

            .title {
              font-size: 4rem;
              color: #2f82ff;
            }

            .number {
              font-size: 3rem;
            }
          }

          .rate {
            flex: 1;
            .progress {
              width: 90%;
              margin: 0 auto;
              border-radius: 0 !important;
            }
          }
        }
      }
    }
  }
</style>



