<template>
  <div id="barrage">

    <div id="barrages-static">
      <transition-group name="fadeLeft" tag="ul">
        <li v-for="(item, index) in getBarrages" :key="index">
          <div class="ava">
            <img :src="item.headpic" alt="">
          </div>
          <div class="content">
            <div class="uname">
              {{ item.name }}
            </div>
            <div class="data" v-html="item.content"></div>
          </div>
        </li>
      </transition-group>
    </div>

  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  props: ['ws'],
  data() {
    return {
    };
  },
  mounted() {
    console.log(this.getBarrages);
    console.log(1);
  },
  watch: {
    getBarrages() {
      setTimeout(() => {
        $("#barrages-static").animate({
          scrollTop: $('#barrages-static ul').height()
        }, 300);
      }, 1000);
    }
  },
  computed: {
    ...mapGetters(["getBarrages"])
  },
}
</script>

<style lang="scss" scoped>
  #barrage {
    position: fixed;
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    color: white;

    #barrages-static {
      width: 100%;
      height: 100%;
      overflow-y: scroll;

      ul {
        margin: 0;
        padding: 0;
        list-style-type: none;

        li {
          min-height: 200px;
          margin-bottom: 20px;
          background-color: rgba(0, 0, 0, .4);
          display: flex;

          .ava {
            width: 200px;
            height: 200px;

            img {
              width: 100%;
              height: 100%;
            }
          }

          .content {
            flex: 1;
            box-sizing: border-box;
            padding: 10px;

            .uname {
              min-height: 50px;
              line-height: 50px;
              font-size: 4rem;
              color: #2f82ff;
            }

            .data {
              font-size: 3rem;
              word-break: break-all;
              
            }
          }
        }
      }
    }
  }
</style>



